import * as yup from "yup";
import { PlanFormValues } from ".";

export const planFormScheme = yup.object<PlanFormValues>().shape({
    topic: yup.string().required(" ").trim(),
    duration: yup.number().required(" ").min(0.1, " 0.1"),
    peoplePlanned: yup.number().required(" ").min(1, " 1"),
    date: yup.string().required(" ").trim(),
    teacher: yup.string().required(" ").trim(),
});
